`, "",{"version":3,"sources":["webpack://./src/shared/components/ncEditor/NcEditor.vue"],"names":[],"mappings":";AA4JA;CACA,aAAA;AACA;AAEA;CACA,WAAA;CACA,gBAAA;AACA;AAEA;CACA,iDAAA;CACA,yCAAA;AACA;AAEA;CACA,wBAAA;AACA;AAEA;CACA,iBAAA;CACA,gBAAA;AACA;;AAEA;0DACA;AACA;CACA,qBAAA;AACA","sourcesContent":["<!--\n  - SPDX-FileCopyrightText: 2023 Nextcloud GmbH and Nextcloud contributors\n  - SPDX-License-Identifier: AGPL-3.0-or-later\n-->\n<template>\n\t<div class=\"editor-wrapper\" :class=\"{ border: showBorder, 'hide-readonly-bar': !showReadonlyBar, 'height-small': height === 'small' }\">\n\t\t<div v-if=\"textAppAvailable\">\n\t\t\t<div ref=\"editor\" />\n\t\t</div>\n\t\t<div v-else>\n\t\t\t<NcEmptyContent\n\t\t\t\t:name=\"t('tables', 'Error')\"\n\t\t\t\t:description=\"t('tables', 'Could not load editor, text not available.')\">\n\t\t\t\t<template #icon>\n\t\t\t\t\t<Alert :size=\"20\" />\n\t\t\t\t</template>\n\t\t\t</NcEmptyContent>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\nimport { NcEmptyContent } from '@nextcloud/vue'\nimport Alert from 'vue-material-design-icons/Alert.vue'\nimport { translate as t } from '@nextcloud/l10n'\n\nexport default {\n\n\tcomponents: {\n\t\tNcEmptyContent,\n\t\tAlert,\n\t},\n\n\tprops: {\n\t\tcanEdit: {\n\t\t      type: Boolean,\n\t\t      default: true,\n\t\t    },\n\t\ttext: {\n\t\t      type: String,\n\t\t      default: '',\n\t\t    },\n\t\tshowBorder: {\n\t\t      type: Boolean,\n\t\t      default: true,\n\t\t    },\n\t\tshowReadonlyBar: {\n\t\t      type: Boolean,\n\t\t      default: true,\n\t\t    },\n\t\theight: {\n\t\t      type: String,\n\t\t      default: null, // null, 'small'\n\t\t    },\n\t},\n\n\tdata() {\n\t\treturn {\n\t\t\ttextAppAvailable: !!window.OCA?.Text?.createEditor,\n\t\t\teditor: null,\n\t\t\tlocalValue: '',\n\t\t}\n\t},\n\n\tcomputed: {\n\t\tlocalText: {\n\t\t\tget() {\n\t\t\t\treturn this.localValue\n\t\t\t},\n\t\t\tset(v) {\n\t\t\t\tthis.localValue = v\n\t\t\t\tthis.$emit('update:text', v)\n\t\t\t},\n\t\t},\n\t},\n\n\twatch: {\n\t\ttext(value) {\n\t\t\tif (value.trim() !== this.localValue.trim()) {\n\t\t\t\tthis.localValue = value\n\n\t\t\t\t// reset editor if content is empty\n\t\t\t\t// otherwise the (empty) content will not be updated\n\t\t\t\tif (value === '') {\n\t\t\t\t\tthis.setupEditor()\n\t\t\t\t} else {\n\t\t\t\t\tthis.editor?.setContent(value)\n\t\t\t\t}\n\t\t\t}\n\t\t},\n\t},\n\n\tasync mounted() {\n\t\tthis.localValue = this.text\n\t\tawait this.setupEditor()\n\t\tthis.editor?.setContent(this.localValue, false)\n\t},\n\n\tbeforeDestroy() {\n\t\tthis?.editor?.destroy()\n\t},\n\n\tmethods: {\n\t\tt,\n\t\tasync setupEditor() {\n\t\t\tthis?.editor?.destroy()\n\t\t\tif (this.textAppAvailable) {\n\t\t\t\tthis.editor = await window.OCA.Text.createEditor({\n\t\t\t\t\tel: this.$refs.editor,\n\t\t\t\t\tcontent: this.localText,\n\t\t\t\t\treadOnly: !this.canEdit,\n\t\t\t\t\tonUpdate: ({ markdown }) => {\n\t\t\t\t\t\tthis.localText = markdown\n\t\t\t\t\t},\n\t\t\t\t})\n\t\t\t} else {\n\t\t\t\tconsole.debug('try to load editor, but not initialized')\n\t\t\t}\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\n\t:deep(.text-editor__wrapper button.entry-action__image-upload) {\n\t\tdisplay: none;\n\t}\n\n\t.editor-wrapper {\n\t\twidth: 100%;\n\t\tmin-width: 200px;\n\t}\n\n\t.editor-wrapper.border {\n\t\tborder: 2px solid var(--color-border-maxcontrast);\n\t\tborder-radius: var(--border-radius-large);\n\t}\n\n\t:deep(.text-readonly-bar) {\n\t\tdisplay: none !important;\n\t}\n\n\t.height-small {\n\t\tmax-height: 320px;\n\t\toverflow-y: auto;\n\t}\n\n\t/* This can be deleted when the patch from text is integrated,\n\tthat sets the z-index of the text-menubar to 1 by default*/\n\t:deep(.text-menubar ) {\n\t\tz-index: 1 !important;\n\t}\n\n</style>\n"],"sourceRoot":""}]);
